@import '../../../styles/abstracts/variables';
@import '../../../styles/base/shadow';
@import '../../../styles/base/selection';
@import '../../../styles/base/text';

.homePane {
  padding: 20px;
}

.contentPane {
  width: 100%;
  height: 100%;
}

.app {
  box-sizing: border-box;
  padding: 5px;
  border-radius: 5px;

  &__icon {
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 5px;
    background-color: white;
    border-radius: 5px;
    border: 2px solid purple;
    @include shadow-drop(black);
    @include undraggable;
  }

  &__title {
    text-align: center;
    color: white;
    @include non-overflow;
  }

  &:hover {
    background-color: rgba(170, 170, 170, 0.5);
  }

  &:active {
    background-color: rgba(109, 109, 109, 0.5);
  }
}

.externalApp {
  text-decoration: none;
}
